* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  font-family: "Montserrat", sans-serif;
  background-color: white;
}
.box {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: URL("./bg.jpg") no-repeat fixed center;
  background-size: cover;
}
form {
  position: relative;
  padding: 50px;
  width: 360px;
  height: 80vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  background: rgb(41.6, 35.3, 80.4, 0.9);
  border-radius: 30px;
  box-shadow: 0 5px 34px rgba(0, 0, 0, 0.3);
  text-align: center;
  font-size: 20px;
  border: 7px solid black;
  color: white;
  font-size: 16px;
  padding-bottom: 20px;
}
form::after {
  content: "";
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
  border-radius: 30px;
  pointer-events: none;
}

img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: 20px;
}

label {
  background: transparent;
}

input {
  line-height: 18px;
  margin: 13px;
  padding: 18px;
  height: 35px;
  width: 100%;
  outline: none;
  font-size: 15px;
  display: inline-block;
  line-height: 100%;
  border-radius: 13px;
  text-transform: uppercase;
  padding-bottom: 18px;
  border: 3px solid black;
}
input:hover {
  background-color: #888;
  cursor: pointer;
  color: white;
}
.checkbox {
  width: 13px;
  height: 13px;
}

form span {
  background: transparent;
  font-size: 12px;
  color: red;
  text-transform: uppercase;
  font-weight: bold;
  padding: 13px;
}
button {
  width: 120px;
  border-radius: 30px;
  padding: 13px;
  border: 3px solid black;
}
button:hover {
  background: slateblue;
  cursor: pointer;
  transition: 0.3s;
}

